.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#mainTitle {
  text-align: left;
  margin-left: 25.3px;
}

#leftArrow {
  float: left;
  width: 2%;
  height: 150px;
  padding: 100px 0;
  opacity: 0.5;
  transition: opacity 0.3s ease;
  background: linear-gradient(270deg, transparent, rgba(0, 0, 0, 0.2));
}

button#leftArrow {
  border-radius: 0%;
}
button#rightArrow {
  border-radius: 0%;
}

#rightArrow {
  float: left;
  width: 2%;
  height: 150px;
  padding: 100px 0;
  opacity: 0.5;
  transition: opacity 0.3s ease;
  background: linear-gradient(90deg, transparent, rgba(0, 0, 0, 0.2));
}

#leftArrow:hover {
  opacity: 1;
}

#rightArrow:hover {
  opacity: 1;
}
#listItem {
  float: left;
  width: 24%;
  text-align: left;
  font-family: "Avenir Next", sans-serif;
  background-color: white;
  z-index: 200;
}

.photo-wrapper {
  overflow: hidden;
  width: 100%;
  display: grid;
  grid-template-columns: 30px 200px 30px;
  grid-template-rows: 100px 60px 30px;
  z-index: 150;
}

.photo-wrapper::before {
  content: " ";
  display: block;
  background-color: red;
  position: relative;
  left: -30px;
  width: 260px;
  bottom: -90px;
  z-index: 1;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.35), transparent 25%);
  pointer-events: none;
  overflow: hidden;
  border-radius: 5px;
}

.photo-wrapper:hover > .item1 {
  opacity: 1;
}

.photo-wrapper:hover > .item3 {
  opacity: 1;
}

.imagesList {
  display: flex;
  width: 100%;
  opacity: 1;
  transition: 0.7s ease;
  position: relative;
}

.images {
  position: relative;
  opacity: 1;
  width: 0px;
  height: 190px;
  transition: 0.5s ease;
}

.images.active {
  position: relative;
  opacity: 1;
  width: 260px;
  height: 190px;
  z-index: 100;
}

.category {
  font-size: 12px;
  color: #ff72b6;
  letter-spacing: 0.4px;
  text-transform: uppercase;
  font-weight: 600;
}

.price {
  font-size: 19px;
  line-height: 19px;
  font-weight: 600;
  margin: 10px 0 4px;
}

.details {
  font-size: 16px;
  font-weight: 400;
  margin-top: 0;
  margin-bottom: 4px;
}

.imageButtons {
  position: absolute;
  float: left;
  overflow: hidden;
  text-align: center;
  right: 0;
  left: 0;
  z-index: 2;
}

.item1 {
  grid-column: 1;
  grid-row: 1 / span 2;
  height: 100%;
  width: 100%;
  padding: 100px 0;
  z-index: 1000;
  opacity: 0;
  border-radius: 5px;
  transition: opacity 0.3s ease;
  background: linear-gradient(270deg, transparent, rgba(0, 0, 0, 0.2));
}
.item2 {
  position: absolute;
  overflow: hidden;
  margin-top: 0;
  grid-column: 2;
  grid-row: 1 / span 2;
  border-radius: 5px;
  display: flex;
}

.item3 {
  grid-column: 3;
  grid-row: 1 / span 2;
  height: 100%;
  width: 100%;
  padding: 100px 0;
  z-index: 1000;
  opacity: 0;
  border-radius: 5px;
  transition: opacity 0.3s ease;
  background: linear-gradient(90deg, transparent, rgba(0, 0, 0, 0.2));
}

.item4 {
  grid-column: 1 / span 3;
  grid-row: 3;
  text-align: center;
  z-index: 1000;
  overflow: hidden;
  margin: auto;
  width: 90px;
}

.imageButtons {
  padding: 0;
  display: flex;
  transition: all 0.2s;
  position: relative;
  margin: 0;
}

.buttons.active {
  color: white;
  border-radius: 50%;
}

button {
  background-color: transparent;
  border: none;
  border-radius: 50%;
  margin: 0;
  color: transparent;
}

button.active:before {
  margin-top: 5px;
  height: 10px;
  width: 10px;
  opacity: 0.8;
}

li {
  list-style-type: none;
}

button:before {
  margin: 0;
  background-color: #fff;
  display: inline-block;
  border-radius: 50%;
  opacity: 0.5;
  height: 6px;
  width: 6px;
  content: "";
  transition: transform 0.35s;
}

#mainTitle {
  color: #1a2258;
  font-family: "Avenir Next", sans-serif;
  font-size: 28px;
  line-height: 40px;
  font-weight: 700;
  margin-bottom: 0;
}
